<template>
  <div style=" height: 100%;">
    <Header :s="status" :t="t"></Header>
    <router-view></router-view>
  </div>
  
</template>

<script>
import Header from './components/Header.vue';

function stop(event) {
  event.currentTarget.acontroller.abort();
}

export default {
  name: 'App',

  components: {
    Header
  },

  // status: begin, scan_baloons, scan_baloons_dlg2, photos, labirint, scan_models
  data: () => ({
    t: '1',
    tasks_completed: [],
    marker_necessary: [],
    marker_type: '',
    current_msg: '',
    current_emoji: '',
    current_delay: 0,
    status_msgs: {
      'begin': [
        {
          text: 'С днем рождения, Любимая!!!',
          delay: 2500,
          emoji: 'max_4',
        },
        {
          text: 'Сегодня в твоей квартире будет праздник. Проходи в комнату, тебя ждет первое задание.',
          delay: 3000,
          emoji: 'max_1',
        },
        {
          text: 'Тебе нужно будет составить слово из букв на шарах. Слово это подскажет путь истинный.',
          delay: 5000,
          emoji: 'max_1'
        },
        {
          text: 'Удачи!',
          delay: 1000,
          emoji: 'max_3',
          next_status: 'scan_baloons_dlg1'
        }
      ],
      'scan_baloons_dlg1': [
      {
          text: 'После составления слов тебе нужно будет сфотографировать его для определения корректности.',
          delay: 10000,
          emoji: 'max_1',
        },
        {
          text: 'Просто наведи камеру на шарики и нажми кнопку "Сделать фото"',
          delay: 3500,
          emoji: 'max_1',
          next_status: 'scan_baloons'
        }
      ],
      // 'scan_baloons': [
        
      // ],
      'scan_models_dlg1': [
        {
          text: 'Верно, умничка)',
          delay: 2000,
          emoji: 'max_6',
        },
        {
          text: 'Слово это - подсказка тебе. Предлагаю использовать камеру и найти праздничное лакомство.',
          delay: 3000,
          emoji: 'max_7',
          next_status: 'scan_models'
        }
      ],
      'scan_models_dlg2': [
        {
          text: 'Хм, похоже это оно. Изучи внимательно, возможно ты что то найдешь...',
          delay: 3000,
          emoji: 'max_2',
          next_status: 'strawberry'
        }
      ],
      'strawberry_found': [
        {
          text: 'Ура, ты нашла вкусную клубничку!',
          delay: 1500,
          emoji: 'max_8'
        },
        {
          text: 'Хочешь пойти дальше?) ',
          delay: 1000,
          emoji: 'max_1'
        },
        {
          text: 'Знаю, что хочешь)',
          delay: 1000,
          emoji: 'max_5'
        },
        {
          text: 'Итак, далее ты отправляешься в свободное плавание. Дам тебе небольшую подсказку - ',
          delay: 3000,
          emoji: 'max_7'
        },
      ],
      'end': [
        {
          text: 'Вот и все, все задания завершены.',
          delay: 2000,
          emoji: 'max_3',
        },
        {
          text: 'С днем рождения, Любимая!!! Пусть этот год принесет тебе море положительных эмоций!',
          delay: 2000,
          emoji: 'max_4',
        }
      ]
    },
    status: 'begin'
  }),
  watch: {
    tasks_completed(v) {
      if (v.includes('taskphotos') && v.includes('labirint')) {
        this.status = 'end';
      }
    }
  },
  methods: {
    async startDialog() {
      const messages = this.status_msgs[this.status];
      if (messages == undefined) {
        this.current_msg = '';
        return;
      }

      const msg_elem = document.querySelector('#msg');

      for (let i = 0; i < messages.length; i++) {
        const msg = messages[i];
        console.log('удален')
        msg_elem.removeEventListener('click', stop);
        this.current_msg = msg.text;
        this.current_emoji = msg.emoji;
        this.current_delay = msg.delay;

        await this.timeout_msg(msg.delay);

        console.log('добавлен')
        let acontroller = new AbortController();
        let signal = acontroller.signal;
        msg_elem.addEventListener('click', stop);
        msg_elem.acontroller = acontroller;

        try {
          await this.timeout_click(signal);
        } catch (err) {
          // console.log();
        }

        if (i == messages.length - 1) {
          this.current_msg = '';
        }

        // eslint-disable-next-line no-prototype-builtins
        if (msg.hasOwnProperty('next_status')) {
          this.status = msg.next_status;
        }
      }
    },
  },
  async mounted() {
  }
};
</script>

