import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

Vue.config.productionTip = false;

import Labirint from './components/Labirint.vue';
import TaskPhotos from './components/TaskPhotos.vue';
import Arjs from './components/Arjs.vue';

const routes = [
  { path: '/', component: Arjs },
  { path: '/labirint', component: Labirint },
  { path: '/taskphotos', component: TaskPhotos }
]

const router = new VueRouter({
  mode: 'history',
  routes
})



new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app')
