<template>
  <div>
    <div style="margin: 10px;">
      <video id="video" :width="$vuetify.breakpoint.width - 20" height="300" autoplay playsinline></video>
      <a v-show="!loading" href="#" id="capture" class="booth-capture-button">Сделать фото</a>
      <canvas id="canvas" width="400" height="300"></canvas>
      <div v-show="loading" style="display: flex; justify-content: center; margin-top: 10px;">
        <v-progress-circular indeterminate color="#8379b5"></v-progress-circular>
      </div>
    </div>
  </div>
</template>

<script>
import { createWorker } from 'tesseract.js';
export default {
  name: 'ScanBaloons',
  data() {
    return {
      loading: false,
      right_words: 'праздничное лакомство',
      current_words: ''
    }
  },
  methods: {
    async recognizePhoto(photo) {
      this.loading = true;
      const worker = await createWorker({
        logger: m => console.log(m)
      });

      (async () => {
        await worker.loadLanguage('rus');
        await worker.initialize('rus');
        const { data: { text } } = await worker.recognize(photo);
        console.log(text);
        // this.$emit('completed');
        this.current_words = text;
        this.checkCorrectWords();
        this.loading = false;
        await worker.terminate();
      })();
    },
    checkCorrectWords() {
      const is_right = this.current_words.toLowerCase().includes(this.right_words);
      if (is_right) {
        console.log('это победа!!!');
        this.$emit('completed');
      } else {
        console.log('Подумайте еще!');
      }
    }
  },
  async mounted() {
    const video = document.getElementById('video');
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');

    const stream = await navigator.mediaDevices.getUserMedia({
      video: {
        facingMode: 'environment'
      }
    });
    video.srcObject = stream;
    video.play();

    document.getElementById('capture').addEventListener('click', () => {
      context.drawImage(video, 0, 0, 400, 300);
      this.recognizePhoto(canvas.toDataURL('image/png'));
    });
  }
}
</script>

<style scoped>
.booth-capture-button {
  display: block;
  margin: 10px 0;
  padding: 10px 20px;
  background: cornflowerblue;
  color: #fff;
  text-align: center;
  text-decoration: none;
}

#canvas {
  display: none;
}
</style>