<template>
  <div>
    <div v-show="status == 'labirint'">
      <div style="display: flex; justify-content: center; margin-top: 4px">
        <div class="grid">
          <div>
            <h2>Текущее значение: {{ currentBall }}</h2>
            <h3 style="margin-bottom: 20px">Требуется: {{ ball_need }}</h3>
          </div>
          <v-btn @click="resetList()" color="#BBDEFB">
            Сбросить
          </v-btn>
        </div>
      </div>
      
      <draggable 
        v-model="list"
        :move="handleMove"
        @end="handleDragEnd"
        v-on:click.native="test"
        delay='60'
        style="width: 310px; position: absolute; left: 0; right: 0; margin: 0 auto"
      >
        <div v-for="(tech, i) in list" :key="i" style="display: inline-block">
          <template v-if="i > 24 && i < 30">
            <div style="background-color: rgb(29, 54, 64); margin: 30px 1px 1px 1px; width: 60px; height: 60px">
              <img
                :id="tech.id" 
                :class="tech.class" 
                class="field" 
                deg="0" 
                :src="getImgUrl(tech.img)" 
                width="60px" 
                height="60px" 
                style="transition: all 0.5s ease-in-out;"
              />
            </div>
          </template>
          <template v-else>
            <div style="background-color: rgb(29, 54, 64); margin: 1px; width: 60px; height: 60px">
            <img style="transition: all 0.5s ease-in-out;" :id="tech.id" :class="tech.class" class="field" deg="0" :src="getImgUrl(tech.img)" width="60px" height="60px"/>
          </div>
          </template>
        </div>
      </draggable>
    </div>
    <Actor :message="current_msg" :emoji="current_emoji" :delay="current_delay"/>
  </div> 
</template>

<script>
import draggable from "vuedraggable";
import Actor from './Actor.vue';

function stop(event) {
  event.currentTarget.acontroller.abort();
}

export default {
    name: 'Labirint',
    components: {
      draggable,
      Actor
    },
    data() {
      return {
        current_msg: '',
        current_emoji: '',
        current_delay: 0,
        status_msgs: {
          'labirint_dlg1': [
            {
              text: 'О, старый добрый лабиринт. Тебе это уже знакомо, поэтому можешь отправляться сразу в бой.',
              delay: 7750,
              emoji: 'max_1',
              next_status: 'labirint'
            }
          ],
          'labirint_dlg2': [
            {
              text: 'Тебе удалось пройти его, Умничка!',
              delay: 2900,
              emoji: 'max_3'
            },
            {
              text: 'Теперь поразмысли - что там может находиться?',
              delay: 4000,
              emoji: 'max_2'
            },
            {
              text: 'Давай проверим, отгадала ли ты. Можешь распаковать.',
              delay: 4500,
              emoji: 'max_1',
            }
          ],
        },
        status: 'labirint_dlg1', // labirint_dlg1 -> labirint -> labirint_dlg2
        types: {
          'place-plus': ['plus'],
          'place-minus': ['minus'],
          'empty': ['line', 'line-turn'],
          'line': ['empty', 'line-turn'],
          'line-turn': ['empty', 'line'],
          'plus': ['plus', 'place_plus'],
          'minus': ['minus', 'place-minus']
        },
        complete: false,
        drop_none: false,
        ball_need: 5,
        movingIndex: 0,
        futureIndex: 0,
        dragging: false,
        list_begin: [
          { id: 0, type: 'empty', count: 0, img: 'empty' },
          { id: 1, type: 'place-plus', count: 0, img: 'place-plus' },
          { id: 2, type: 'empty', count: 0, img: 'empty' },
          { id: 3, type: 'empty', count: 0, img: 'block', blocked: true },
          { id: 4, type: 'empty', count: 0, img: 'block', blocked: true },

          { id: 5, type: 'place-plus', count: 0, img: 'place-plus' },
          { id: 6, type: 'empty', count: 0, img: 'empty' },
          { id: 7, type: 'empty', count: 0, img: 'empty' },
          { id: 8, type: 'place-plus', count: 0, img: 'place-plus' },
          { id: 9, type: 'empty', count: 0, img: 'empty' },

          { id: 10, type: 'empty', count: 0, img: 'empty' },
          { id: 11, type: 'empty', count: 0, img: 'empty' },
          { id: 12, type: 'empty', count: 0, img: 'end', blocked: true, class: 'rotate-180' },
          { id: 13, type: 'place-minus', count: 0, img: 'place-minus' },
          { id: 14, type: 'empty', count: 0, img: 'empty' },

          { id: 15, type: 'empty', count: 0, img: 'empty' },
          { id: 16, type: 'place-plus', count: 0, img: 'place-plus' },
          { id: 17, type: 'empty', count: 0, img: 'empty' },
          { id: 18, type: 'empty', count: 0, img: 'line-turn-block', blocked: true, class: 'rotate-180' },
          { id: 19, type: 'place-plus', count: 0, img: 'place-plus' },

          { id: 20, type: 'empty', count: 0, img: 'block', blocked: true },
          { id: 21, type: 'empty', count: 0, img: 'block', blocked: true },
          { id: 22, type: 'empty', count: 0, img: 'start', blocked: true },
          { id: 23, type: 'empty', count: 0, img: 'empty' },
          { id: 24, type: 'empty', count: 0, img: 'empty' },

          { id: 25, type: 'line', count: 2, img: 'line' },
          { id: 26, type: 'line', count: 2, img: 'line' },
          { id: 27, type: 'line', count: 2, img: 'line' },
          { id: 28, type: 'line-turn', count: 1, img: 'line-turn' },
          { id: 29, type: 'line-turn', count: 1, img: 'line-turn' },

          { id: 30, type: 'line-turn', count: 1, img: 'line-turn' },
          { id: 31, type: 'line-turn', count: 1, img: 'line-turn' },
          { id: 32, type: 'line-turn', count: 1, img: 'line-turn' },
          { id: 33, type: 'line-turn', count: 1, img: 'line-turn' },
          { id: 34, type: 'line-turn', count: 1, img: 'line-turn' },

          { id: 35, type: 'plus', count: 1, img: 'plus-5' },
          { id: 36, type: 'plus', count: 1, img: 'plus-4' },
          { id: 37, type: 'plus', count: 1, img: 'plus-2' },
          { id: 38, type: 'minus', count: 1, img: 'minus-2' },
          { id: 39, type: 'minus', count: 1, img: 'minus-1' }
        ],
        list: []
      };
    },
    computed: {
      currentBall() {
        const items = this.list.filter((item, index) => {
          if (index < 25 && (item.type == 'plus' || item.type == 'minus')) return true;
        });
        if (!items.length > 0) return 0;
        const current = items.reduce((accumulator, currentValue) => {
          if (currentValue.type == 'plus') {
            return accumulator + Number(currentValue.img.slice(-1));
          }
          if (currentValue.type == 'minus') {
            return accumulator - Number(currentValue.img.slice(-1));
          }
        }, 0)
        return current;
      }
    },
    watch: {
      list(v) {
        this.check(v);
      }
    },
    methods: {
      timeout_msg(delay) {
        return new Promise(resolve => setTimeout(resolve, delay));
      },
      timeout_click(signal) {
        return new Promise((resolve, reject)=> {
          const error = new DOMException('aborted!', 'AbortError');

          const timeout = setTimeout(() => {
            resolve('you report!'); 
          }, 600000);

          if (signal.aborted) {
            clearTimeout(timeout);
            return reject(error);//[2]
          }

          signal.addEventListener('abort',() => {
            clearTimeout(timeout);
            reject(error);
          });
        });
      },
      async startDialog() {
        const messages = this.status_msgs[this.status];
        if (messages == undefined) {
          this.current_msg = '';
          return;
        }

        const msg_elem = document.querySelector('#msg');

        for (let i = 0; i < messages.length; i++) {
          const msg = messages[i];
          console.log('удален')
          msg_elem.removeEventListener('click', stop);
          this.current_msg = msg.text;
          this.current_emoji = msg.emoji;
          this.current_delay = msg.delay;

          await this.timeout_msg(msg.delay);

          console.log('добавлен')
          let acontroller = new AbortController();
          let signal = acontroller.signal;
          msg_elem.addEventListener('click', stop);
          msg_elem.acontroller = acontroller;

          try {
            await this.timeout_click(signal);
          } catch (err) {
            // console.log();
          }

          if (i == messages.length - 1) {
            this.current_msg = '';
          }

          // eslint-disable-next-line no-prototype-builtins
          if (msg.hasOwnProperty('next_status')) {
            this.status = msg.next_status;
          }
        }
      },
      resetList() {
        this.list = JSON.parse(JSON.stringify(this.list_begin));
        
      },
      check(v) {
        console.log('start');
        for (let i = 0; i < v.length; i++) {
          switch(i) {
            case 6: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 180) {
                  return;
                }
              }
              break;
            }
            case 7: {
              if (v[i].type != 'line') {
                return;
              } else {
                const item_deg = Number(document.getElementById(v[i].id).getAttribute('deg')) % 360;
                if (item_deg != 0 && item_deg != 180) {
                  return;
                }
              }
              break;
            }
            case 8: {
              if (v[i].type != 'plus') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 0) {
                  return;
                }
              }
              break;
            }
            case 9: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 270) {
                  return;
                }
              }
              break;
            }
            case 10: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 180) {
                  return;
                }
              }
              break;
            }
            case 11: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 0) {
                  return;
                }
              }
              break;
            }
            case 13: {
              if (v[i].type != 'minus') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 180) {
                  return;
                }
              }
              break;
            }
            case 14: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 0) {
                  return;
                }
              }
              break;
            }
            case 15: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 90) {
                  return;
                }
              }
              break;
            }
            case 16: {
              if (v[i].type != 'plus') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 180) {
                  return;
                }
              }
              break;
            }
            case 17: {
              if (v[i].type != 'line-turn') {
                return;
              } else {
                if ((Number(document.getElementById(v[i].id).getAttribute('deg')) % 360) != 270) {
                  return;
                }
              }
              break;
            }
          }
        }
        if (this.currentBall == this.ball_need) {
          this.complete = true;
          this.status = 'labirint_dlg2';
          this.startDialog();
          // this.$emit('completed');
        }
      },
      getImgUrl(img) {
        const images = require.context('../assets/', false, /\.png$/);
        return images('./' + img + ".png");
      },
      handleDragEnd() {
      if (this.drop_none) {
        this.drop_none = false;
        return;
      }
      this.futureItem = this.list[this.futureIndex];
      this.movingItem = this.list[this.movingIndex];
      const _items = Object.assign([], this.list);
      _items[this.futureIndex] = this.movingItem;
      _items[this.movingIndex] = this.futureItem;

      // обнуляем повороты
      const current_elem = document.getElementById(this.movingItem.id);
      current_elem.setAttribute('deg', 0);
      current_elem.style.transform = 'rotate(0deg)';

      const future_elem = document.getElementById(this.futureItem.id);
      future_elem.setAttribute('deg', 0);
      future_elem.style.transform = 'rotate(0deg)';

      this.list = _items;
    },
      handleMove(e) { // при помещении на кого то
        const elem_target = e.relatedContext;
        if (elem_target.element.blocked) {
          this.drop_none = true;
          return false;
        }
        const { index, futureIndex, element } = e.draggedContext;
        if (element.blocked) {
          this.drop_none = true;
          return false;
        }

        // проверяем по схожести
        if (!this.types[elem_target.element.type].includes(element.type)) {
          this.drop_none = true;
          return false;
        }

        this.movingIndex = index;
        this.futureIndex = futureIndex;
        this.drop_none = false;
        return false; // disable sort
      },
      test(event) {
        if (event.target.id == '') return;
        const item = this.list.find((i) => i.id === Number(event.target.id));
        if (item == undefined) return;
        if (item.blocked) return;
        const elem = event.target;
        let next_deg_number = 0;
        const deg_number = elem.getAttribute('deg');
        next_deg_number = Number(deg_number) + Number(90);
      
        elem.setAttribute('deg', next_deg_number);
        elem.style.transform = 'rotate('+next_deg_number+'deg)'
        setTimeout(() => {
          this.check(this.list);
        }, 500);
      }
    },
    mounted() {
      this.list = JSON.parse(JSON.stringify(this.list_begin));
      this.startDialog();
    }
}
</script>

<style>

.field {
  transform: rotate(0deg);
}

.anim {
  /* animation: 0.5s linear rotate; */
  transition: all 2s ease-in-out;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(90deg);
  }
}

.rotate-90 {
  transform: rotate(90deg);
}

.rotate-180 {
  transform: rotate(180deg);
}

.rotate-270 {
  transform: rotate(270deg);
}

.grid {
  display: grid;
  grid-template-columns: 1fr 0.5fr;
  grid-template-rows: 1fr;
  grid-column-gap: 50px;
  grid-row-gap: 0px;
}

</style>